<template>
<div>
	<h2 class="title" style="background:#F7F7F7;">{{ListData.title}}</h2>
	<article class="excerpt" v-for="data in ListData.data">
	  <div class="focus">
	    <a :href="data.url" class="thumbnail">
	      <img :src="data.imgSrc" :alt="data.newSketch"/>
	    </a>
	  </div>
	  <header>
	    <h2>
	      <a :href="data.url" :title="data.newsName" >{{data.newsName}}</a>
	    </h2>
	  </header>
	  <p class="note">{{data.newSketch}}</p>
	  <p style="margin-top:40px;">
	    <span class="muted"><i class="icon-user icon12"></i> {{data.newsAuthor}}</span>
	    <span class="muted"><i class="ico icon-time icon12"></i> {{data.newsTime.split("T")[0]}}</span>
	    <span class="muted"><i class="ico icon-eye-open icon12"></i> {{data.viewCount}}次浏览</span>
	    <span class="muted"> 个人博客：[<a href='Javascript:'> {{data.classify}}</a>]</span>
	  </p>
	</article>
</div>
</template>
<script>
	export default{
		data(){
			return{
				 
			}
		},
		mounted:function(){
		},
		components:{

		},
		methods:{

		},
		props:["ListData"]
	}
</script>

<style scoped>
.title {
  position: relative;
  margin: 0;
  padding: 6px 20px;
  height: 20px;
  border-radius: 5px 5px 0 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px
}
.excerpt {
	position: relative;
	margin-bottom: 1px;
	padding: 20px 20px 40px 256px;
	border-left: solid 4px #fff;
	background-color: #fff
}
.excerpt header {
	margin: 5px 40px 5px 0
}
.excerpt .focus {
	position: relative;
	float: left;
	margin-top: 0;
	margin-left: -240px;
	width: 220px;
	text-align: center
}
.excerpt .thumbnail img {
	display: block;
	width: 220px;
	height: 150px
}
.excerpt .thumbnail:hover {
	border-color: #ccc;
	border-bottom-color: #bbb
}
.excerpt h2 {
	position: relative;
	top: 1px;
	display: inline;
	margin: 0;
	font-weight: 700;
	font-size: 16px;
	line-height: 25px
}
.excerpt h2 strong {
	margin-right: 10px;
	color: #8CAC52
}
.excerpt .note {
	color: #888;
	font-size: 14px;
	line-height: 24px
}
.excerpt .itag a {
	display: inline-block;
	margin-right: 3px;
	padding: 0 5px;
	border-color: #e2e2e2 #ddd #ddd #e2e2e2;
	border-style: solid;
	border-width: 1px;
	border-radius: 1px;
	line-height: 18px
}
</style>


